import { Component } from '@angular/core';
import { MessageService } from '../../services/message.service';

@Component({
  selector: 'my-playlistpage',
  template: require('./playlistpage.component.html'),
  styles: [require('./playlistpage.component.css')]
})
export class PlaylistPageComponent {
    private showUpdateMessage = false;
    private refreshPlayList = false;
    constructor( public messageService: MessageService ) {}

    public refreshList() {
        this.messageService.showMessage = false;
        this.messageService.updateList.next();
    }
}



